<template>
  <el-dialog
    v-model="dialogVisible"
    :title="$t('swap.history')"
    width="420px"
    custom-class="tokens"
  >
    <div class="flex-column-start tokens-content">
        <div class="flex-h__1 tokens-list">
            <div 
                class="tokens-list__item pointer flex-row-start align-center pointer"
                :class="{'text-red': item.status == 0}"
                v-for="(item, index) in history"
                :key="index"
                @click="selectHandler(item)"
            >
                <div class="flex-1">
                    <div class="token-list__item-title">Swap {{item.item1.price}} {{item.item1.symbol}} for {{item.item2.price}} {{item.item2.symbol}}</div>
                </div>
                <el-icon :size="30" color="red" class="ML-10" v-if="item.status == 0"><Close /></el-icon>
                <el-icon :size="30" color="#fff" class="ML-10" v-else><Check /></el-icon>
            </div>
        </div>
        <el-empty description="Empty History" v-if="history.length <= 0"></el-empty>
    </div>
  </el-dialog>
</template>

<script>
import { utils } from 'ethers'
import { getBit, Tips } from '../../../libs/utils'
import { mapGetters } from 'vuex'
export default {
    name: 'History',
    data() {
        return {
            utils,
            getBit,
            dialogVisible: false,
            history: []
        }
    },
    computed: {
        ...mapGetters(['currentNetwork', 'currentDex']),
    },
    
    methods: {
        open() {
            this.dialogVisible = true
            const history = localStorage.getItem(window.wallet.accounts[0]+this.currentDex.address + 'swapHistory')
            this.history = history ? JSON.parse(history) : []
            this.history = this.history.reverse()
        },
        selectHandler(item) {
            window.open(`https://bscscan.com/tx/${item.tx.hash}`)
        }
    }
}
</script>

<style lang="scss">
.tokens-list__import-name{
    font-size: 16px;
    font-weight: bold;
    color: #fff;
}
.tokens-list__import-desc{
    font-size: 14px;
    color: rgb(165, 165, 165);
}
.tokens-list__import-btn,.tokens-list__import-btn:hover{
    background: #FCBE2F;
    border: 1px solid #2A2A2A;
    color: #fff;
}
    .tokens{
        background: rgb(39, 38, 44);
        border-radius: 32px;
        max-height: 100vh;
        max-width: 100%;
        display: flex;
        flex-direction: column;

        .el-dialog__close{
            font-size: 20px;
            font-weight: bold;
        }
        .el-dialog__title{
            color: #FFF;
        }
        .el-dialog__header{
            border-bottom: 1px solid #5d5d5d;
        }
        .el-dialog__body{
            flex: 1;
            height: 100%;
            overflow: hidden;
            padding: 0;
        }
        .tokens-content{
            height: 100%;
        }
        .tokens-input{
            background-color: rgb(55, 47, 71);
            border-radius: 16px;
            color: rgb(244, 238, 255);
            display: block;
            font-size: 16px;
            height: 48px;
            outline: 0px;
            padding: 0px 16px;
            width: calc(100% - 60px);
            border: 1px solid rgb(38, 33, 48);
            margin: 20px;
        }
        .tokens-list{
            overflow: auto;
            max-height: 400px;
            &::-webkit-scrollbar {
                width: 8px;
            }
            &::-webkit-scrollbar-thumb {
                background: rgb(184, 173, 210);
                border-radius: 8px;
            }
            &::-webkit-scrollbar-track {
                border-radius: 10px;
            }
            .tokens-list__item{
                padding: 20px 30px;
                transition: all .3s;
                &:hover{
                    background: rgb(8, 6, 11);
                }
                &.is-disabled{
                    background: #333;
                    opacity: .5;
                    cursor: not-allowed;
                }
                &.text-red{
                    .token-list__item-title{
                        color: red;
                    }
                }
            }
            .tokens-list__item-icon{
                width: 24px;
                height: 24px;
            }
            .token-list__item-title{
                color: #fff;
                font-size: 16px;
                font-weight: bold;
            }
            .token-list__item-desc{
                color: #999;
            }
            .token-list__item-price{
                font-size: 14px;
                color: #fff;
            }
        }
    }
</style>