<template>
  
  <div class="swap flex-row-start">
    <el-tabs v-model="activeName" class="swap-tabs" @tab-click="tabChange">
      <el-tab-pane :label="$t('swap.title')" name="Swap" lazy>
        <div class="swap-left MR-20" v-if="chartShow">
          <marquee
            class="rank-lst flex-row-center"
            behavior="scroll"
            onmouseover="this.stop()"
            onmouseout="this.start()">
            <div class="flex-row-start align-center rank-lst__info">
              <div 
                class="rank-lst__item flex-row-start align-center pointer" 
                v-for="item in rankList" :key="item.address"
                @click="refreshPage(item)">
                <img :src="item.logo" class="rank-lst__item--logo" v-if="item.logo"/>
                <div class="rank-lst__item--logo" v-else>{{item.symbol.substr(0,1)}}</div>
                <div>{{ item.symbol }}</div>
              </div>
            </div>
          </marquee>
          <iframe class="swap-iframe" :src="getChartUrl" frameborder="0"></iframe>  
        </div>
        <div class="swap-info PB-20">
          <div class="swap-top">
            <el-icon :size="30" class="swap-top__icon" @click="chartShow = !chartShow"><Histogram /></el-icon>
            <div class="swap-info__title MB-10">{{ t('swap.title') }}</div>
            <div class="swap-info__title-info">{{ t('swap.desc') }}</div>
            <img src="@/assets/images/rpc.png" class="swap-top__icon rpc pointer" @click="openRpcList"/>
            <el-icon :size="30" class="swap-top__icon right pointer" v-if="isLogin" @click="openHistory"><Clock /></el-icon>
          </div>
          <div class="swap-item">
            <div class="swap-item__top flex-row-between">
              <div class="flex-row-start align-center pointer">
                <div class="flex-row-start align-center" @click="openTokensDialog('item1')">
                  <img v-lazy="getLogo(item1.address)" class="swap-item__top-icon MR-10"/>
                  <div class="swap-item__top-name text-overflow__1">{{item1.symbol}}</div>
                  
                  <el-icon class="ML-5">
                    <ArrowDown />
                  </el-icon>
                </div>
                <el-icon class="ML-5" @click="copyHandler(item1.address)"><CopyDocument /></el-icon>
              </div>
              <div v-if="isLogin">
                <el-icon class="is-loading" v-if="item1.loading">
                  <Loading />
                </el-icon>
                <template v-else-if="item1.balance">
                  {{ t('swap.balance') }}{{getBit(utils.formatUnits(item1.balance, item1.decimals), 5) }}
                </template>
              </div>
            </div>
            <div class="swap-item__input MT-10 flex-row-start align-center" :class="{'is-disabled': !isLogin}">
              <input :disabled="!isLogin || approveLoading" class="text-right flex-1" v-bit @input="price1Change('item1')" v-model="item1.price" placeholder="0.00">
              <el-tag class="ML-10 ml-2 pointer" effect="light" v-if="isLogin" @click="maxHandler" type="warning" size="small">MAX</el-tag>
              <div class="swap-item__input-usdt">
                {{ getUsdt(item1.price, item1.address, 'item1') }}
                <template v-if="item1.usdt && item1.price">~${{ item1.usdt }}</template>
              </div>
            </div>
            
          </div>
          <img src="@/assets/images/transform.png" class="swap-transform pointer" @click="transformHandler" />
          <div class="swap-item">
            <div class="swap-item__top flex-row-between">
              <div class="flex-row-start align-center pointer">
                <div class="flex-row-start align-center" @click="openTokensDialog('item2')">
                  <img v-lazy="getLogo(item2.address)" class="swap-item__top-icon MR-10"/>
                  <div class="swap-item__top-name text-overflow__1">{{item2.symbol}}</div>
                  <el-icon class="ML-5">
                    <ArrowDown />
                  </el-icon>
                </div>
                <el-icon class="ML-5" @click="copyHandler(item2.address)"><CopyDocument /></el-icon>
                <img class="ML-5 swap-item__top-wallet" v-if="isLogin && connectState == 1" src="@/assets/images/metamask.png" @click="addTokenHandler"/>
              </div>
              <div v-if="isLogin">
                <el-icon class="is-loading" v-if="item2.loading">
                  <Loading />
                </el-icon>
                <template v-else-if="item2.balance">
                  {{ t('swap.balance') }}{{getBit(utils.formatUnits(item2.balance, item2.decimals), 5)}}
                </template>
              </div>
            </div>
            <div class="swap-item__input MT-10 flex-row-start align-center" :class="{'is-disabled': !isLogin}">
              <input :disabled="!isLogin || approveLoading" v-model="item2.price"  v-bit class="text-right flex-1" @input="price1Change('item2')" placeholder="0.00">
              <div class="swap-item__input-usdt">
                {{ getUsdt(item2.price, item2.address, 'item2') }}
                <template v-if="item2.usdt && item2.price">~${{ item2.usdt }}</template>
              </div>
            </div>
           
          </div>
          <div class="swap-slippage PL-20 PR-20 MT-10">
            
            <div class="swap-slippage__select flex-row-between pointer">
              <div class="text-16">GWEI</div>
              <div class="flex-row-end">
                <el-tag 
                  class="swap-slippage__select-tag" 
                  type="warning" 
                  :effect="gWei == item ? 'dark' : 'light'" 
                  @click="toggleGwei(item)"
                  v-for="item in gWeiList"
                  :key="item">
                  {{ item }}
                </el-tag>
                <input type="number" class="swap-slippage__select-input" placeholder="5" v-model.number="gWei">
              </div>
            </div>
          </div>
          <div class="PL-30 PR-30 MT-15">
            <div class="flex-row-between MB-10">
              <div class="text-yellow">{{ t('swap.tolerance') }}</div>
              <div class="text-blue">{{progress}}%</div>
            </div>
            <div>
              <el-slider v-model="progress" :min="1" :max="99" @input="progressChange"/>
              <!--div class="flex-row-between text-yellow">
                <div>0%</div>
                <div>100%</div>
              </div-->
            </div>
          </div>
          <el-button v-if="!isLogin" class="swap-btn MT-30" @click="loginFn">{{ t('swap.connectWallet') }}</el-button>
          <el-button v-else-if="!hasApprove" :loading="approveLoading" class="swap-btn MT-30" @click="approveHandler">{{ t('pledge.approve') }}</el-button>
          <template v-else>
            <el-button v-if="!item1.price" disabled class="swap-btn MT-15"> {{ t('swap.enterAmount') }} </el-button>
            <el-button v-else class="swap-btn MT-15" :loading="swapLoading" @click="swapSubmit">{{ t('swap.title') }}</el-button>
          </template>
          <div class="flex-row-between ML-30 MR-30 MT-20">
            <el-checkbox v-model="professional" @change="checkChange" size="large">
              <div class="flex-row-center">
                {{ $t('swap.professional') }}
                <a target="_blank" href="https://docs.teadao.app/chan-pin/jiao-huan/ru-he-jiao-yi-how-to-trade#zhuan-ye-mo-shi">
                  <el-icon class="ML-5"><QuestionFilled /></el-icon>
                </a>
              </div>
            </el-checkbox>
            <el-checkbox v-model="intellectRouter" :disabled="!professional" label="" size="large" />
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane :label="$t('swap.limit')" name="Limit" lazy>
        <AutoSetting class="swap-left MR-20" @copy="copyHandler"/>
      </el-tab-pane>
      <el-tab-pane label="Swap" name="BulkSwap" lazy>
        <SwapVue />
      </el-tab-pane>
    </el-tabs>
  </div>
  <Tokens ref="tokens" @change="tokenChange" @update="tokenUpdate"/>
  <SwapConfirm ref="swapConfirm" :confirm="swapHandler" :amountOutMin="getBit(utils.formatUnits(amountOutMin.toString(), item2.decimals), 5)"/>
  <History ref="history" />
  <RpcList ref="rpcList" />

  <el-drawer
    v-model="chartShow"
    direction="btt"
    :show-close="false"
    custom-class="swap-drawer"
    modal-class="swap-drawer__modal"
    append-to-body
    :with-header="false"
  >
    <div class="flex-row-start align-center MB-5 MT-20 MR-20">
      <el-button v-if="!isLogin" class="swap-btn" @click="loginFn">{{ t('swap.connectWallet') }}</el-button>
      <el-button v-else-if="!hasApprove" :loading="approveLoading" class="swap-btn" @click="approveHandler">{{ t('pledge.approve') }}</el-button>
      <template v-else>
        <el-button v-if="!item1.price" disabled class="swap-btn"> {{ t('swap.enterAmount') }} </el-button>
        <el-button v-else class="swap-btn" :loading="swapLoading" @click="swapSubmit">{{ t('swap.title') }}</el-button>
      </template>
      <el-icon :size="30" color="#fff" class="el-icon--left" @click="chartShow = false"><CircleCloseFilled /></el-icon>
    </div>
    <marquee
        class="rank-lst flex-row-center"
        behavior="scroll"
        onmouseover="this.stop()"
        onmouseout="this.start()">
        <div class="flex-row-start align-center rank-lst__info">
          <div 
            class="rank-lst__item flex-row-start align-center pointer" 
            v-for="item in rankList" :key="item.address"
            @click="refreshPage(item)">
            <img :src="item.logo" class="rank-lst__item--logo" v-if="item.logo"/>
            <div class="rank-lst__item--logo" v-else>{{item.symbol.substr(0,1)}}</div>
            <div>{{ item.symbol }}</div>
          </div>
        </div>
      </marquee>
    <iframe class="swap-iframe" :src="getChartUrl" frameborder="0"></iframe>
  </el-drawer>
</template>

<script>
import Tokens from './modal/tokens.vue'
import SwapConfirm from './modal/swapConfirm.vue'
import RpcList from './modal/rpcList.vue'
import copy from 'copy-text-to-clipboard'
import { ElNotification } from 'element-plus'
import { utils, BigNumber } from 'ethers'
import { getBit, Tips, getLogo, setDocumentTitle } from '../../libs/utils'
import { mapGetters, mapActions } from 'vuex'
import networkList from '@/api/abis/network.js'
import History from './modal/history.vue'
import i18n from '@/locals/index.js'
import { toRaw } from 'vue'
import axios from 'axios'
import AutoSetting from './auto.vue'
import SwapVue from '../tools/components/Swap.vue'
const { t } = i18n.global
let USDT_ADDRESS = '0x55d398326f99059fF775485246999027B3197955'
const BUSD_ADDRESS = '0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56'
let path = [];
let addressPath = [];
export default {
  data() {
    return {
      t,
      utils,
      getLogo,
      getBit,
      chartShow: false,
      // 
      intellectRouter: true,
      // 
      professional: false,
      // loading
      swapLoading: false,
      // loading
      approveLoading: false,
      // 
      hasApprove: false,
      activeName: 'Swap',
      progress: 10,
      // 
      isLogin: false,
      // 10
      rankList: [],
      gWei: 5,
      gWeiList: [5, 7, 10, 15, 20],
      // 
      swapHistory: [],
      type: '',
      timer: null,
      balanceTimer: null,
      rankTimer: null,
      // 
      connectState: '',
      // 
      item1: {
        name: "BNB",
        symbol: "BNB",
        address: "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c",
        balance: '',
        price: '',
        loading: false
      },
      // 
      item2: {
        name: "BUSD",
        symbol: "BUSD",
        address: "0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56",
        balance: '',
        price: '',
        loading: false
      }
    }
  },
  components: {
    Tokens,
    History,
    SwapConfirm,
    RpcList,
    AutoSetting,
    SwapVue
  },
  watch: {
    'currentNetwork.ChainID': {
      immediate: true,
      handler() {
        const { Token } = this.currentNetwork
        const item1 = Token[0]
        const item2 = Token[1] || Token[0]
        this.item1 = {...this.item1, ...item1}
        this.item2 = {...this.item2, ...item2}
        this.gWei = 5 * this.currentNetwork.GWEI
        this.gWeiList = [
          5 * this.currentNetwork.GWEI, 
          7 * this.currentNetwork.GWEI, 
          10 * this.currentNetwork.GWEI, 
          15 * this.currentNetwork.GWEI, 
          20 * this.currentNetwork.GWEI
        ]
      }
    },
  },
  directives: {
    bit: {
      updated(el) {
        el.value = el.value ? getBit(el.value, 5) : ''
      }
    }
  },
  computed: {
    ...mapGetters(['currentNetwork', 'networkList', 'currentDex']),
    amountOutMin() {
      if (!this.item2.price) {
        return BigNumber.from(0)
      }
      let price = utils.parseUnits(this.item2.price.toString(), this.item2.decimals)
      let price1 = utils.parseUnits(this.item2.price.toString(), this.item2.decimals)
      let progress = utils.parseUnits((this.progress).toString(), this.item2.decimals)
      let num100 = utils.parseUnits('100', this.item2.decimals)
      return price1.sub(price.mul(progress).div(num100))
    },
    getChartUrl() {
      // 
      const list = this.currentNetwork.transferChartToken
      const item = list.find(item => {
        return item.address === this.item2.address
      })
      if (item) {
        return `https://coinbrain.com/embed/bnb-${this.item1.address}?theme=custom&accent=3f3f3f&padding=16&background=242424&chart=1&trades=1&ratio=0.7`
      } else {
        return `https://coinbrain.com/embed/bnb-${this.item2.address}?theme=custom&accent=3f3f3f&padding=16&background=242424&chart=1&trades=1&ratio=0.7`
      }
      
    }
  },
  beforeMount() {    
    const { chart, type } = this.$route.query
    this.chartShow = chart == 1 ? true : false
    this.activeName = type ? type : this.activeName
    // dex
    const key = this.$route.params.key || networkList[0].Dex[0].symbol
    let dexIndex;
    const netWorkIndex = networkList.findIndex(item => {
      dexIndex = item.Dex.findIndex(sub => {
        return sub.symbol === key
      })
      if (dexIndex >= 0) {
        return item
      }
    })
    this.setCurrentDex({
      netWorkIndex,
      dexIndex
    })
  },
  mounted() {
    // usdt
    if (this.currentNetwork.ChainID === '0x7d0') {
      USDT_ADDRESS = '0x765277EebeCA2e31912C9946eAe1021199B39C61'
    }
    
    const progress = localStorage.getItem('progress')
    this.progress = progress ? JSON.parse(progress) : 10
    this.isLogin = window.wallet.accounts[0] ? true : false
    this.isLogin && this.loginCallBack()
    window.wallet.on('accountChange', () => {
      this.isLogin = window.wallet.accounts[0] ? true : false
      if (!this.isLogin) {
        this.clearTimer()
      }
      this.isLogin && this.loginCallBack()
    })
    this.getRankList()
    this.rankTimer = setInterval(() => {
      this.getRankList()
    }, 5 * 60 * 1000)
  },
  beforeUnmount () {
    this.clearTimer()
  },
  methods: {
    ...mapActions(['setCurrentDex', 'setConnectDialog']),
    tabChange() {
      setDocumentTitle(`Tea Dao-${this.activeName}`)
    },
    getUsdt(price, address, key) {
      if (!price || address.toLocaleLowerCase() == BUSD_ADDRESS.toLocaleLowerCase() || address.toLocaleLowerCase() == USDT_ADDRESS.toLocaleLowerCase()) {
        this[key].usdt = ''
        return
      }
      let promises = []
      promises = [window.wallet.ContractWETH.getAmountsOut(utils.parseUnits(price, this[key].decimals), [
        address,
        USDT_ADDRESS
      ])]
      this.currentNetwork.TransferTokens.forEach(item => {
        if (item.address != address && item.address != USDT_ADDRESS) {
          promises.push(window.wallet.ContractWETH.getAmountsOut(utils.parseUnits(price, this[key].decimals), [
            address,
            item.address,
            USDT_ADDRESS
          ]))
        }
      })
      Promise.allSettled(promises).then(data => {
        let result = '';
        data.forEach((item) => {
          const {status, value} = item
          if (status == "fulfilled") {
            const last = value[value.length - 1]
            if (!result) {
              result = last
            } else if (last.gt(result)) {
              result = last
            }
          }
        })
        const contract = window.wallet.initContract(USDT_ADDRESS)
        contract.decimals().then(decimals => {
          this[key].usdt = getBit(utils.formatUnits(result.toString(), decimals), 3)
        })
      })
    },
    checkChange() {
      if (!this.professional) {
        this.intellectRouter = true
      }
    },
    // rpclist
    openRpcList() {
      this.$refs.rpcList.open(window.wallet.connectState)
    },
    // 
    getRankList() {
      axios.post('https://api.coinbrain.com/cointoaster/coins?size=100&sort=priceChange24h%3Adesc').then(data => {
        let res = data.data.items.filter(item => {
          const { totalLiquidityUsd, trades24h, logo } = item
          if (totalLiquidityUsd > 10000 && trades24h > 50 && logo ) {
            return item
          }
        })
        this.rankList = res.sort((a, b) => b.trades24h - a.trades24h).slice(0, 10)
      })
    },
    refreshPage(data) {
      this.type = 'item2'
      this.$refs.tokens.fastImport(data.address, false)
    },
    clearTimer() {
      clearInterval(this.timer)
      clearTimeout(this.balanceTimer)
      clearInterval(this.rankTimer)
      this.timer = null 
      this.balanceTimer = null
      this.rankTimer = null
    },
    // 
    addTokenHandler() {
      const { symbol, address } = this.item2
      window.wallet.addTokenToMeta(address, symbol, getLogo(address));
    },
    // 
    progressChange() {
      localStorage.setItem('progress', this.progress)
    },
    // 
    transformHandler(){
      const res = toRaw(this.item1)
      this.item1 = toRaw(this.item2)
      this.item2 = res
    },
    maxHandler() {
      let price = toRaw(this.item1.balance)
      // 0.01
      if (this.item1.address === this.currentNetwork.MainCoin.address) {
        const data = utils.parseUnits('0.01', this.item1.decimals)
        price = price.sub(data)
      }
      this.item1.price = utils.formatUnits(price, this.item1.decimals)
      this.price1Change('item1')
    },
    // 
    async swapHandler () {
      let promise;
      let gaslimit
      const payableAmount = utils.parseUnits(this.item1.price.toString(), this.item1.decimals)
      const amountOutMin = this.amountOutMin
      const to = window.wallet.accounts[0]
      const deadline = utils.parseUnits((new Date().getTime() + 15 * 60 * 1000).toString(), this.item1.decimals)
      // 
      if (this.item1.address == this.currentNetwork.MainCoin.address) {
        try {
          gaslimit = await window.wallet.ContractWETH.estimateGas[this.currentNetwork.MainCoin_TO_OTHER](
            amountOutMin,
            path,
            to,
            deadline,
            {
              value: payableAmount,
              gasPrice: utils.parseUnits(this.gWei.toString(), 9),
            }
          )
          promise = window.wallet.ContractWETH[this.currentNetwork.MainCoin_TO_OTHER](
            amountOutMin,
            path,
            to,
            deadline,
            {
              value: payableAmount,
              gasPrice: utils.parseUnits(this.gWei.toString(), 9),
              gasLimit: gaslimit.mul(135).div(100)
            }
          )
        } catch (error) {
          Tips(error)
        }
        
      } else if(this.item2.address == this.currentNetwork.MainCoin.address){
        try{
          gaslimit = await window.wallet.ContractWETH.estimateGas[this.currentNetwork.OTHER_TO_MainCoin](
            payableAmount,
            amountOutMin,
            path,
            to,
            deadline,
            {
              gasPrice: utils.parseUnits(this.gWei.toString(), 9),
            }
          )
          
          // 
          promise = window.wallet.ContractWETH[this.currentNetwork.OTHER_TO_MainCoin](
            payableAmount,
            amountOutMin,
            path,
            to,
            deadline,
            {
              gasPrice: utils.parseUnits(this.gWei.toString(), 9),
              gasLimit: gaslimit.mul(135).div(100)
            }
          )
        } catch (error) {
          Tips(error)
        }
      } else {
        try{
          gaslimit = await window.wallet.ContractWETH.estimateGas.swapExactTokensForTokensSupportingFeeOnTransferTokens(
            payableAmount,
            amountOutMin,
            path,
            to,
            deadline,
            {
              gasPrice: utils.parseUnits(this.gWei.toString(), 9),
            }
          )
          // 
          promise = window.wallet.ContractWETH.swapExactTokensForTokensSupportingFeeOnTransferTokens(
            payableAmount,
            amountOutMin,
            path,
            to,
            deadline,
            {
              gasPrice: utils.parseUnits(this.gWei.toString(), 9),
              gasLimit: gaslimit.mul(135).div(100)
            }
          )
        } catch (error) {
          Tips(error)
        }
      }
      if (!promise) {
        return
      }
      this.swapLoading = true
      try {
        const tx = await promise
        try {
          clearInterval(this.timer)
          await tx.wait()
          ElNotification({
            type: t('swap.success'),
            title: t('swap.success'),
            message: t('swap.tradeSuccess')
          })
          this.loginCallBack()
          this.swapHistory.push({
            item1: JSON.parse(JSON.stringify(this.item1)),
            item2: JSON.parse(JSON.stringify(this.item2)),
            tx: JSON.parse(JSON.stringify(tx)),
            status: 1
          })
        } catch (error) {
          this.swapHistory.push({
            item1: JSON.parse(JSON.stringify(this.item1)),
            item2: JSON.parse(JSON.stringify(this.item2)),
            tx: JSON.parse(JSON.stringify(tx)),
            status: 0
          })
          Tips(error)
        }
        this.price1Change('item1')
        localStorage.setItem(window.wallet.accounts[0]+this.currentDex.address+'swapHistory', JSON.stringify(this.swapHistory))
      } catch (error) {
        Tips(error)
      }
      
      this.swapLoading = false
    },
    // 
    async price1Change(key) {
      let otherKey = key == 'item1' ? 'item2' : 'item1'
      this[key].price = getBit(this[key].price.replace(/[^0-9.]/g,''), 5) 
      if (this[key].price == '') {
        this[key].price = ''
        this[otherKey].price = ''
        return
      }
      const price = this[key].price.toString()
      if (price === '0.') {
        return
      }
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        this.price1Change(key)
      }, 300)
  
      const bool = this.currentNetwork.TransferTokens.find(item => {
        return item.address.toLocaleLowerCase() === this.item2.address.toLocaleLowerCase()
      })
      let funKey = key == 'item1' ? 'getAmountsOut' : 'getAmountsIn'
      let promises = []
      promises = [window.wallet.ContractWETH[funKey](utils.parseUnits(price, this[key].decimals), [
        this.item1.address,
        this.item2.address
      ])]
      addressPath = [[this.item1.address,this.item2.address]]
      // 
      if (this.intellectRouter) {
        this.currentNetwork.TransferTokens.forEach(item => {
          if (item.address != this[key].address && item.address != this[otherKey].address) {
            addressPath.push([this.item1.address,item.address,this.item2.address])
            promises.push(window.wallet.ContractWETH[funKey](utils.parseUnits(price, this[key].decimals), [
              this.item1.address,
              item.address,
              this.item2.address
            ]))
            // 
            if (!bool) {
              this.currentNetwork.TransferTokens.forEach(sub => {
                if (item.address != sub.address) {
                  addressPath.push([this.item1.address,item.address, sub.address, this.item2.address])
                  promises.push(window.wallet.ContractWETH[funKey](utils.parseUnits(price, this[key].decimals), [
                    this.item1.address,
                    item.address,
                    sub.address,
                    this.item2.address
                  ]))
                }
              })
            }
          }
        })
      }
      
      Promise.allSettled(promises).then(data => {
          let result = '';
          let lastIndex;
          data.forEach((item, index) => {
            const {status, value} = item
            if (status == "fulfilled") {
              const last = key == 'item1' ? value[value.length - 1] : value[0]
              if (!result) {
                result = last
                lastIndex = index
              } else if (last.gt(result)) {
                result = last
                lastIndex = index
              }
            }
          })
          if (result && this[key].price && this[key].price != '0') {
            // console.log(lastIndex, ''+utils.formatUnits(result.toString(), this[otherKey].decimals)+'-----------'+result.toString())
            this[otherKey].price = utils.formatUnits(result, this[otherKey].decimals)
            path = addressPath[lastIndex]
          }
      })
    },
    async loginCallBack(loading = true) {
      clearTimeout(this.balanceTimer)
      this.item1.loading = loading
      this.item2.loading = loading
      this.connectState = window.wallet.connectState
      const history = localStorage.getItem(window.wallet.accounts[0]+this.currentDex.address+'swapHistory')
      this.swapHistory = history ? JSON.parse(history): []
      this.getItemBalance(this.item1.address, 'item1').then(({balance, decimals}) => {
        this.item1.balance = balance
        this.item1.decimals = decimals
        this.item1.loading = false
      })
      this.getItemBalance(this.item2.address, 'item2').then(({balance, decimals}) => {
        this.item2.balance = balance
        this.item2.decimals = decimals
        this.item2.loading = false
      })
      this.balanceTimer = setTimeout(() => {
        this.loginCallBack(false)
      }, 3000)
    },
    // 
    async getItemBalance(address, keyType) {
      if (!address) {
        return
      }
      let balance;
      const contract = window.wallet.initContract(address)
      const decimals = await contract.decimals()
      if (address == this.currentNetwork.MainCoin.address) {
        this.hasApprove = true
        balance = await window.wallet.provider.getBalance()
      } else {
        if (keyType == 'item1') {
          try {
            const data = await contract.allowance(window.wallet.accounts[0], this.currentDex.address)
            const res = Math.floor(Number(utils.formatUnits(data, decimals)))
            if (res <= 0) {
              this.hasApprove = false
              // 
              if (this.connectState == 3) {
                await this.approveHandler()
              }
            } else {
              this.hasApprove = true
            }
          } catch (error) {
            console.log(error)
            // Tips(error)
          }
        }
        balance = await contract.balanceOf(window.wallet.accounts[0])
      }
      return {
        balance,
        decimals
      }
    },
    // 
    async approveHandler() {
      this.approveLoading = true
      try {
        const contract = window.wallet.initContract(this.item1.address)
        const tx = await contract.approve(
          this.currentDex.address,
          utils.parseEther('10000000000000'),
          {gasPrice: utils.parseUnits('10', 9)}
        )
        await tx.wait();
        this.hasApprove = true
      } catch (error) {
        Tips(error)
      }
      this.approveLoading = false
    },
    // 
    tokenUpdate({address, name, symbol}) {
      if (this.item1.address == address) {
        this.item1.name = name
        this.item1.symbol = symbol
      } else if (this.item2.address == address) {
        this.item2.name = name
        this.item2.symbol = symbol
      }
    },
    // 
    async tokenChange(data, type) {
      type = type || this.type
      if (type == 'item1') {
        if (this.item2.address == data.address) {
          this.item2 = JSON.parse(JSON.stringify(this.item1))
        }
      } else {
        if (this.item1.address == data.address) {
          if (this.item1.address == data.address) {
            this.item1 = JSON.parse(JSON.stringify(this.item2))
          }
        }
      }
      data.price = data.price || '0'
      this[type] = data
      this.isLogin && this.loginCallBack()
      this.price1Change(type == 'item1' ? 'item2' : 'item1')
    },
    copyHandler(text) {
      copy(text)
      ElNotification({
        type: t('swap.success'),
        title: t('swap.success'),
        message: t('swap.copySuccess')
      })
    },
    toggleGwei(data) {
      this.gWei = data
    },
    openHistory() {
      this.$refs.history.open()
    },
    openTokensDialog(type) {
      this.type = type
      this.$refs.tokens.open([this.item1.address, this.item2.address,])
    },
    swapSubmit() {
      if (this.professional) {
        this.swapHandler()
      } else {
        this.$refs.swapConfirm.open(this.item1, this.item2)
      }
    },
    // 
    loginFn() {
      this.setConnectDialog(true)
    }
  }
}
</script>

