<template>
    <div class="flex-row-center flex-1 flex-start wrapper">
      <div class="wrapper-box" :class="{'back': isBack}">
        <div class="swap-info PB-20 imputation">
        <div class="swap-item">
            <div class="swap-item__top flex-row-between">
                <div class="flex-row-start align-center pointer">
                    <div class="flex-row-start align-center" @click="openTokensDialog('item1')">
                    <img v-lazy="getLogo(item1.address)" class="swap-item__top-icon MR-10"/>
                    <div class="swap-item__top-name text-overflow__1">{{item1.symbol}}</div>
                    <el-icon class="ML-5">
                        <ArrowDown />
                    </el-icon>
                    </div>
                    <el-icon class="ML-5" @click="copyHandler(item1.address)"><CopyDocument /></el-icon>
                </div>
            </div>
        </div>
        <img src="@/assets/images/transform.png" class="swap-transform pointer" @click="transformHandler"/>
        <div class="swap-item">
            <div class="swap-item__top flex-row-between">
                <div class="flex-row-start align-center pointer">
                    <div class="flex-row-start align-center" @click="openTokensDialog('item2')">
                    <img v-lazy="getLogo(item2.address)" class="swap-item__top-icon MR-10"/>
                    <div class="swap-item__top-name text-overflow__1">{{item2.symbol}}</div>
                    <el-icon class="ML-5">
                        <ArrowDown />
                    </el-icon>
                    </div>
                    <el-icon class="ML-5" @click="copyHandler(item2.address)"><CopyDocument /></el-icon>
                </div>
            </div>
        </div>
        
        <textarea class="imputation-textarea" v-model="sendAddress" placeholder=""></textarea>
        <el-button class="swap-btn MT-10" :disabled="!sendAddress" :loading="searchLoading" @click="searchHandler"></el-button>
        </div>
        <div class="flex-1 wrapper-box__back flex-column-start" v-if="isBack">
          <div class="flex-h__1">
          <table class="imputation-table"> 
              <thead>
              <tr>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th>{{item1.symbol}}</th>
                  <th></th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="(item, index) in tableList" :key="index">
                  <td>{{ item1.symbol }}</td>
                  <td>{{ item2.symbol }}</td>
                  <td>{{item.provider ? showAddress(item.provider.address) : '--'}}</td>
                  <td>{{ item.balance || '--' }}</td>
                  <td>
                      
                    <el-icon class="is-loading" v-if="item.loading">
                        <Loading />
                    </el-icon>
                    <template v-else>
                        {{ item.message }}
                    </template>
                  </td>
              </tr>
              </tbody>
          </table>
        </div>
        <div class="swap-slippage MT-20">
            <div class="swap-slippage__select flex-row-between pointer">
              <div class="text-18"></div>
              <div class="flex-row-end">
                <el-switch
                  v-model="braveTroopsTest"
                  size="large"
                  class="swap-switch"
                />
              </div>
            </div>
        </div>
        <div class="swap-slippage MT-20">
            <div class="swap-slippage__select flex-row-between pointer">
              <div class="text-18"></div>
              <div class="flex-row-end">
                <el-switch
                  v-model="transformMode"
                  size="large"
                  class="swap-switch"
                  active-text=""
                  inactive-text=""
                />
              </div>
            </div>
        </div>
        <div class="swap-slippage MT-20 MB-20">
            <div class="swap-slippage__select flex-row-between pointer">
            <div class="text-18">GWEI</div>
            <div class="flex-row-end">
                <el-tag 
                class="swap-slippage__select-tag" 
                type="warning" 
                :effect="gWei == item ? 'dark' : 'light'" 
                @click="toggleGwei(item)"
                v-for="item in gWeiList"
                :key="item">
                {{ item }}
                </el-tag>
                <input type="number" class="swap-slippage__select-input" placeholder="5" v-model.number="gWei">
            </div>
            </div>
        </div>
        <div>
          <div class="flex-row-between">
            <div>{{ t('swap.tolerance') }}</div>
            <div class="text-blue">{{progress}}%</div>
          </div>
          <div>
            <el-slider v-model="progress" :min="1" :max="99" @input="progressChange"/>
          </div>
        </div>
        <div v-if="!transformMode" class="MT-10">
          <div class="flex-row-between">
            <div></div>
            <div class="text-blue">{{transformRate}}%</div>
          </div>
          <div>
            <el-slider v-model="transformRate" :min="1" :max="99" @input="progressChange"/>
          </div>
        </div>
        <div class="flex-row-start align-center MB-10 MT-10" v-else>
            <div class="text-18 MR-30"></div>
            <div class="swap-item__input flex-1 flex-row-start align-center">
            <input class="text-right flex-1" v-model="price" placeholder="0.00">
            </div>
        </div>
        <div class="flex-row-start align-center">
          <el-icon :size="40" class="pointer" v-if="!hasLoading" @click="isBack = false"><Back /></el-icon>
          <el-button class="swap-btn MT-10" :loading="hasLoading" :disabled="transformMode && !price" @click="swapHandler"></el-button>
        </div>
        </div>
    </div>
  </div>
  <Tokens ref="tokens" @change="tokenChange" @update="tokenUpdate"/>
</template>

<script>
import Tokens from '../../swap/modal/tokens.vue'
import copy from 'copy-text-to-clipboard'
import { ElNotification } from 'element-plus'
import BLKTokenABI from '@/api/abis/IERC20.json'
import { utils } from 'ethers'
import { getBit, getLogo } from '../../../libs/utils'
import ABI from '@/api/abis/SwapHelper.json'
import { product } from '@/plugins/wallet/config.json'
import { mapGetters, mapActions } from 'vuex'
import i18n from '@/locals/index.js'
import { toRaw } from 'vue'
const { t } = i18n.global
export default {
  data() {
    return {
      t,
      utils,
      getLogo,
      getBit,
      // 
      progress: 10,
      loading: false,
      // loading
      searchLoading: false,
      // 
      transformMode: false,
      // 
      braveTroopsTest: false,
      // 
      transformRate: 10,
      isBack: false,
      gWei: 5,
      gWeiList: [5, 7, 10, 15, 20],
      type: '',
      price: '',
      fee: '',
      txPrice: '',
      // 
      sendAddress: '',
      tableList: [],
      // 
      item1: {
        name: "BNB",
        symbol: "BNB",
        address: "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c",
        balance: '',
        price: '',
        loading: false
      },
      // 
      item2: {
          name: "BUSD",
          symbol: "BUSD",
          address: "0xe9e7CEA3DedcA5984780Bafc599bD69ADd087D56",
          balance: '',
          price: '',
          loading: false
      }
    }
  },
  components: {
    Tokens,
  },
  watch: {
    'currentNetwork.ChainID': {
      immediate: true,
      handler() {
        const { Token } = this.currentNetwork
        const item1 = Token[0]
        const item2 = Token[1] || Token[0]
        this.item1 = {...this.item1, ...item1}
        this.item2 = {...this.item2, ...item2}
        this.gWei = 5 * this.currentNetwork.GWEI
        this.gWeiList = [
          5 * this.currentNetwork.GWEI, 
          7 * this.currentNetwork.GWEI, 
          10 * this.currentNetwork.GWEI, 
          15 * this.currentNetwork.GWEI, 
          20 * this.currentNetwork.GWEI
        ]
      }
    },
  },
  directives: {
    bit: {
      updated(el) {
        el.value = el.value ? getBit(el.value, 5) : ''
      }
    }
  },
  computed: {
    ...mapGetters(['currentNetwork', 'networkList', 'currentDex']),
    hasLoading() {
      const item = this.tableList.find(item => {
        return item.loading
      })
      return !!item
    }
  },
  methods: {
    ...mapActions(['setCurrentDex', 'setConnectDialog']),
    // 
    async searchHandler() {
      this.searchLoading = true
      const sendList = this.sendAddress.split('\n')
      let decimals
      const list = []
      const promises = []
      for (let i = 0; i < sendList.length; i++) {
        const item = sendList[i];
        let provider
        let status
        let message
        let contract
        try {
          provider = window.wallet.getPrivateKeyProvider(item)
          contract = window.wallet.initContract(this.item1.address, BLKTokenABI, provider)
          if (!decimals) {
            decimals = await contract.decimals()
          }
          if (this.item1.address == this.currentNetwork.MainCoin.address) {
            promises.push(provider.getBalance())
          } else {
            promises.push(contract.balanceOf(provider.address))
          }
        } catch (error) {
          status = -1
          message = error.data ? error.data.message || error.message : error.reason || error.message
        }
        list.push({
          provider,
          balance: 0,
          status,
          message,
          contract,
          decimals,
          loading: false
        })
      }
      const data = await Promise.allSettled(promises)
      data.forEach((item, index) => {
        if (item.status === "fulfilled") {
          list[index].balance = getBit(utils.formatUnits(item.value.toString(), decimals), 6),
          list[index].status = 0
        } else {
          list[index].balance = ''
          list[index].status = -1
          list[index].message = ''
        }
      })
      this.tableList = list
      this.searchLoading = false
      this.isBack = true
    },
    // 
    async getPath(decimals) {
      const bool = this.currentNetwork.TransferTokens.find(item => {
        return item.address.toLocaleLowerCase() === this.item2.address.toLocaleLowerCase()
      })
      let promises = []
      promises = [window.wallet.ContractWETH.getAmountsOut(utils.parseUnits('1', decimals), [
          this.item1.address,
          this.item2.address
      ])]
      let addressPath = [[this.item1.address, this.item2.address]]
      // 
      if (this.intellectTest) {
          this.currentNetwork.TransferTokens.forEach(item => {
              if (item.address != this.item1.address && item.address != this.item2.address) {
                  addressPath.push([this.item1.address,item.address,this.item2.address])
                  promises.push(window.wallet.ContractWETH.getAmountsOut(utils.parseUnits('1', decimals), [
                      this.item1.address,
                      item.address,
                      this.item2.address
                  ]))
                  // 
                  if (!bool) {
                      this.currentNetwork.TransferTokens.forEach(sub => {
                          if (item.address != sub.address) {
                              addressPath.push([this.item1.address,item.address, sub.address, this.item2.address])
                              promises.push(window.wallet.ContractWETH.getAmountsOut(utils.parseUnits('1', decimals), [
                              this.item1.address,
                                  item.address,
                                  sub.address,
                                  this.item2.address
                              ]))
                          }
                      })
                  }
              }
          })
      }
      const data = await Promise.allSettled(promises)
      let result = '';
      let lastIndex;
      data.forEach((item, index) => {
          const {status, value} = item
          if (status == "fulfilled") {
          const last = value[value.length - 1]
          if (!result) {
              result = last
              lastIndex = index
          } else if (last.gt(result)) {
              result = last
              lastIndex = index
          }
          }
      })
      const path = addressPath[lastIndex]
      return path
    },
    // 
    getPrice(decimals, balance) {
      if (this.transformMode) {
        return utils.parseUnits(this.price.toString(), decimals)
      } else {
        balance = utils.parseUnits(balance.toString(), decimals)
        const transformRate = utils.parseUnits(this.transformRate.toString(), decimals)
        const hundred = utils.parseUnits('100', decimals)
        return balance.mul(transformRate).div(hundred)
      }
    },
    // 
    async swapHandler () {
      let path
      let fee;
      let txPrice;
      for (let i = 0; i < this.tableList.length; i++) {
        this.tableList[i].loading = true
        const item = toRaw(this.tableList[i]);
        const contract = window.wallet.initContract(product.SWAP_HELPER, ABI, item.provider)
        // 
        if (!fee) {
          const [one, two] = await Promise.all([contract.callFee(), contract.checkTxPrice()])
          fee = one
          txPrice = two
        }
        // 
        if (!path) {
          path = await this.getPath(item.decimals)
        }
        const price = this.getPrice(item.decimals, item.balance)
        let valueBigNumber = utils.parseUnits('0', 18)
        valueBigNumber = valueBigNumber.add(fee)
        // 
        if (this.braveTroopsTest) {
            valueBigNumber = valueBigNumber.add(txPrice)
        }
        // value
        if (this.item1.address === this.currentNetwork.MainCoin.address) {
            valueBigNumber = valueBigNumber.add(price)
        }
        try {
            contract.estimateGas.manyTokensBuy(
              price, 
              1, 
              this.progress, 
              this.braveTroopsTest, 
              [item.provider.address], 
              path, 
              {
                  value: valueBigNumber,
                  gasPrice: utils.parseUnits(this.gWei.toString(), 9),
              }
            ).then( gasLimit => {
              contract.manyTokensBuy(
                  price, 
                  1, 
                  this.progress, 
                  this.braveTroopsTest, 
                  [item.provider.address], 
                  path, 
                  {
                      value: valueBigNumber,
                      gasPrice: utils.parseUnits(this.gWei.toString(), 9),
                      gasLimit: gasLimit.mul(135).div(100)
                  }
              ).then(tx => {
                tx.wait().then(() => {
                  this.tableList[i].status = 1
                  this.tableList[i].loading = false
                  this.tableList[i].message = ''
                }).catch(error => {
                  console.log(error)
                  this.tableList[i].status = -1
                  this.tableList[i].loading = false
                  this.tableList[i].message = error.data ? error.data.message || error.message : error.reason || error.message
                })
              }).catch(error => {
                console.log(error)
                this.tableList[i].status = -1
                this.tableList[i].loading = false
                this.tableList[i].message = error.data ? error.data.message || error.message : error.reason || error.message
              })
            }).catch(error => {
              console.log(error)
              this.tableList[i].status = -1
              this.tableList[i].loading = false
              this.tableList[i].message = error.data ? error.data.message || error.message : error.reason || error.message
            })
        } catch (error) {
          console.log(error)
          this.tableList[i].status = -1
          this.tableList[i].loading = false
          this.tableList[i].message = error.data ? error.data.message || error.message : error.reason || error.message
        }
      }
    },
    showAddress(address) {
      return '0x...' + address.slice(-4)
    },
    // 
    tokenUpdate({name, symbol}) {
      this.item1.name = name
      this.item1.symbol = symbol
    },
    // 
    async tokenChange(data) {
      this.item1 = data
    },
    copyHandler(text) {
      copy(text)
      ElNotification({
        type: t('swap.success'),
        title: t('swap.success'),
        message: t('swap.copySuccess')
      })
    },
    toggleGwei(data) {
      this.gWei = data
    },
    openTokensDialog(type) {
      this.type = type
      this.$refs.tokens.open()
    },
    // 
    transformHandler(){
      const res = toRaw(this.item1)
      this.item1 = toRaw(this.item2)
      this.item2 = res
    },
  }
}
</script>

<style lang="scss">
.swap-switch{
  .el-switch__label.el-switch__label--left, .el-switch__label.el-switch__label--right{
    color: #fff;
    &.is-active{
      color: #E8BE1B;
    }
  }
}
</style>

<style lang="scss" scoped>
  .flex-h__1{
    overflow: auto;
  }
  .wrapper{
    .wrapper-box{
      position: relative;
      width: 100%;
      
      .wrapper-box__back{
        position: absolute;
        z-index: 20;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #181818;
        border: 1px solid #2A2A2A;
        border-radius: 18px;
        padding: 20px;
      }
    }
  }
  .text-blue{
    font-size: 16px;
    color: #1fc7d5;
  }
    .imputation-table{
      width: 100%;
      border: 1px solid #2A2A2A;
      border-radius: 18px;
      border-spacing: 0;
      th{
        padding: 10px 0;
        border-right: 1px solid #2A2A2A;
        &:last-child{
          border-right: none;
        }
      }
      td {
        text-align: center;
        border-top: 1px solid #2A2A2A;
        border-right: 1px solid #2A2A2A;
        word-break: break-all;
        width: 20%;
        padding: 10px;
        &:last-child{
          border-right: none;
        }
      } 
    }
  .imputation-textarea{
    margin: 10px 30px;
    background: none;
    border: 1px solid #898989;
    border-radius: 8px;
    width: calc(100% - 60px);
    height: 350px;
    resize: none;
    color: #fff;
    padding: 10px;
  }
  .imputation-input{
    margin: 10px 30px;
    background: none;
    border: 1px solid #898989;
    border-radius: 8px;
    width: calc(100% - 60px);
    height: 40px;
    color: #fff;
    padding: 10px;
  }
  .swap-item__input{
    width: 100%;
    height: 40px;
    border: 1px solid #2A2A2A;
    border-radius: 8px;
    background: none;
    font-size: 14px;
    padding: 0 20px;
    color: #fff;
    position: relative;
    >input{
      width: 100%;
      height: 100%;
      background: none;
      color: #fff;
      font-size: 16px;
    }
  }
  .swap-btn{
    margin: 0 auto;
    width: calc(100% - 60px);
    height: 60px;
    background: #FCBE2F;
    border: 1px solid #2A2A2A;
    border-radius: 18px;
    color: #fff;
    display: block;
    &.is-disabled{
      background: #e4e7ed !important;
    }
    &:hover, &:focus, &:active{
      background: #FCBE2F;
      color: #fff;
      border: none;
    }
  }
  </style>