<template>
  <el-dialog
    v-model="dialogVisible"
    :title="$t('swap.confirmSwap')"
    width="620px"
    custom-class="confirm"
  >
    <div class="flex-row-between confirm-item">
        <div class="flex-row-start align-center">
            <img v-lazy="getLogo(item1.address)" class="confirm-item__icon MR-5"/>
            {{getBit(item1.price, 9)}}
        </div>
        <div>{{item1.symbol}}</div>
    </div>
    <el-icon color="#fff" :size="20" class="MT-20 MB-20"><Bottom /></el-icon>
    <div class="flex-row-between confirm-item">
        <div class="flex-row-start align-center">
            <img v-lazy="getLogo(item2.address)" class="confirm-item__icon MR-5"/>
           {{getBit(item2.price, 9)}}
        </div>
        <div>{{item2.symbol}}</div>
    </div>
    <!-- <div class="text-white MT-30">Output is estimated. You will receive at least 15.7901 BUSD or the transaction will revert.</div> -->
    <div class="confirm-info">
        <div class="flex-row-between MB-10">
            <div>{{ t('swap.price') }}</div>
            <div class="flex-row-end">
                {{getBit(item2.price / item1.price, 5)}} {{item2.symbol}} / {{item1.symbol}}
            </div>
        </div>
        <div class="flex-row-between MB-10">
            <div class="flex-row-start align-center">
                <div>{{ t('swap.minimum') }}</div>
                <el-tooltip placement="right">
                    <template #content> {{t('swap.tips1')}},<br/> {{t('swap.tips2')}}. </template>
                    <el-icon class="ML-5 pointer"><QuestionFilled /></el-icon>
                </el-tooltip>
            </div>
            <div class="flex-row-end">
                {{ amountOutMin }} {{item2.symbol}}
            </div>
        </div>
    </div>
    <el-button class="confirm-btn MT-30" :loading="loading" @click="confirmHandler">{{ t('swap.confirmSwap') }}</el-button>
  </el-dialog>
</template>

<script>
import { utils } from 'ethers'
import { getBit, getLogo } from '../../../libs/utils'
export default {
    name: 'swapConfirm',
    props: {
        amountOutMin: String,
        confirm: Function
    },
    data() {
        return {
            utils,
            getBit,
            getLogo,
            dialogVisible: false,
            item1: {},
            item2: {},
            loading: false
        }
    },
    methods: {
        open(item1, item2) {
            this.item1 = item1
            this.item2 = item2
            this.dialogVisible = true
        },
        confirmHandler() {
            this.loading = true
            this.confirm().finally(() => {
                this.loading = false
                this.dialogVisible = false
            })
        }
    }
}
</script>

<style lang="scss">
    .confirm{
        background: rgb(39, 38, 44);
        border-radius: 32px;
        max-height: 100vh;
        max-width: 100%;
        display: flex;
        flex-direction: column;
        color: #fff;
        .el-dialog__title{
            color: #FFF;
        }
        .el-dialog__close{
            font-size: 20px;
            font-weight: bold;
        }
        .el-dialog__header{
            border-bottom: 1px solid #5d5d5d;
        }
        .el-dialog__body{
            flex: 1;
            height: 100%;
            overflow: hidden;
            padding: 20px 30px;
            color: #fff;
        }
        .confirm-item{
            color: #fff;
            font-size: 18px;
            font-weight: bold;
        }
        .confirm-item__icon{
            width: 24px;
            height: 24px;
        }
        .confirm-info{
            margin-top: 24px;
            padding: 16px;
            border-radius: 16px;
            border: 1px solid rgb(56, 50, 65);
            background-color: rgb(8, 6, 11);
        }
        .text-green{
            color: rgb(49, 208, 170);
        }
        .confirm-btn{
            width: 100%;
            height: 48px;
            border-radius: 18px;
            height: 60px;
            background: #FCBE2F;
            border: 1px solid #2A2A2A;
            border-radius: 18px;
            color: #fff;
            &.is-disabled{
                background: #e4e7ed;
            }
        }
    }
</style>